<template>
  <div>

    <el-dialog
      title="分析内容"
      :visible.sync="dialogForm.dialogVisible"
      width="50%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div style="height:700px">
        <editor @onEditorChange="onEditorChange" :value="dialogForm.AnalysisContent" style="height:640px"></editor>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogForm.dialogVisible = false" size="small">取 消</el-button>
        <el-button type="primary" @click="saveAnalysisContent" size="small">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="添加/修改"
      :visible.sync="dialogFormUpd.dialogVisible"
      width="30%"
      :before-close="handleClose"
      :close-on-click-modal ="false">
      <div slot="title" style="text-align: left;">
          <div>{{dialogFormUpd.type=='1'?'添加':'修改'}}</div>
          <hr style="height:1px;border:none;border-top:1px solid #E0E0E0;" />
      </div>      
      <div style="display:table;width:100%;border-collapse:separate; border-spacing:5px 10px;text-align: left;margin-left:20px">
        
        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">持仓状态</div>
            <div style="display:table-cell;">
                <span>
                  <el-select size="small" v-model="dialogFormUpd.PositionStatus" placeholder="请选择" style="width:220px">
                    <el-option
                      v-for="item in dialogFormUpd.PositionStatusOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </span>
            </div>
        </div>

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">建仓时间</div>
            <div style="display:table-cell">
                <span>
                  <el-date-picker
                    v-model="dialogFormUpd.PositionStartTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="small">
                  </el-date-picker>
                </span>
            </div>
        </div>        

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">清仓时间</div>
            <div style="display:table-cell">
                <span>
                  <el-date-picker
                    v-model="dialogFormUpd.PositionEndTime"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:ss:mm"
                    size="small">
                  </el-date-picker>
                </span>
            </div>
        </div>          

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">证券代码</div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CompanyCode" placeholder="证券代码" size="small" style="width:220px"></el-input>
            </div>
        </div>      

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">证券名称</div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CompanyName" placeholder="证券名称" size="small" style="width:220px"></el-input>
            </div>
        </div>      

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">持仓数量</div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.PositionVol" placeholder="持仓数量" size="small" style="width:220px"></el-input>
            </div>
        </div>    

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">成本价格</div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CostPrice" placeholder="成本价格" size="small" style="width:220px"></el-input>
            </div>
        </div>    

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">当前价格</div>
            <div style="display:table-cell">
                <el-input v-model="dialogFormUpd.CurrentPrice" placeholder="当前价格" size="small" style="width:220px"></el-input>
            </div>
        </div>  

        <div style="display:table-row">
            <div style="display:table-cell;vertical-align:top">交易市场</div>
            <div style="display:table-cell">
                <span>
                  <el-select size="small" v-model="dialogFormUpd.TradeMkPlace" placeholder="请选择" style="width:220px">
                    <el-option
                      v-for="item in dialogFormUpd.TradeMkPlaceOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </span>
            </div>
        </div>                                      

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormUpd.dialogVisible = false" size="small">取 消</el-button>
        <el-button type="primary" @click="savePositionAnalysis" size="small">确 定</el-button>
      </span>
    </el-dialog>    

    <!-- 页面主体 -->
    <div class="boxmain">

      <!-- 检索条件     -->
      <div>

        <div>
          <el-input v-model="formData.CompanyCode" placeholder="证券代码" size="mini" style="width:150px"></el-input>
          <el-select v-model="formData.PositionStatus" placeholder="持仓状态" size="mini" style="width:150px;margin-left:10px" @change="search()">
            <el-option
              v-for="item in formData.PositionStatusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>     
          <el-button type="info" @click="search()" size="mini" style="margin-left:10px">检索</el-button>   
        </div>

        <div>
          <el-button type="info" @click="viewDialogUpd(1,null)" size="mini" style="margin-top:10px">添加</el-button>   
        </div>

      </div>
    
      <!--统计信息-->
      <div style="margin-top:0px;">
        <div style="display:table;width:100%;border-collapse:separate; border-spacing:0px 10px;">
          <div style="display:table-row">
            <div style="display:table-cell">
                <span>共：{{total}}</span>
            </div>
          </div>
        </div>
      </div>

      <!--列表信息-->
      <div>

        <!--检索结果-->
        <el-table v-loading="loading"
            :data="viewData.gridData"
            style="font-size: 13px;width: 100%;margin-bottom: 20px;"
            :header-cell-style="({row, rowIndex})=>{return 'background-color:#F5F5F5;text-align:center;'}"
            :cell-style="({row, column, rowIndex, columnIndex})=>{return 'padding:0px 0px'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              type="index"
              :index="indexMethod"
              width="40">
            </el-table-column>            
            <!-- <el-table-column type="selection"
                          label=""
                          width="30">
            </el-table-column> -->
            <el-table-column label="注" prop="" width="50" align="center">
              <template slot-scope="scope">
                <el-link icon="el-icon-edit"  type="primary" @click="viewDialog(scope.row)"></el-link>
                <span v-if="scope.row.AnalysisContent">*</span>
              </template>
            </el-table-column>  
            <el-table-column label="持仓状态" prop="PositionStatus" width="80" v-if="formData.PositionStatus==''" align="center">
               <template slot-scope="scope">
                <span v-if="scope.row.PositionStatus=='1'">持仓中</span>
                <span v-if="scope.row.PositionStatus=='2'">已清仓</span>
              </template>
            </el-table-column> 
            <el-table-column label="建仓时间" prop="PositionStartTime" width="90" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatData(scope.row.PositionStartTime)}}</span>
              </template>
            </el-table-column>    
            <el-table-column label="清仓时间" prop="PositionEndTime" width="90" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatData(scope.row.PositionEndTime)}}</span>
              </template>              
            </el-table-column>                           
            <el-table-column label="证券代码" prop="CompanyCode" width="80" align="center">
            </el-table-column> 
            <el-table-column label="证券名称" prop="CompanyName" width="80" align="center">
            </el-table-column>   
            <el-table-column label="持仓数量" prop="PositionVol" width="80" align="center">
            </el-table-column> 
            <el-table-column label="成本价格" prop="CostPrice" width="80" align="center">
            </el-table-column>  
            <el-table-column label="当前价格" prop="CurrentPrice" width="80" align="center">
            </el-table-column>  
            <el-table-column label="盈亏比例" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix(((scope.row.CurrentPrice-scope.row.CostPrice)/scope.row.CostPrice)*100,3)}}%</span>
              </template>                
            </el-table-column>  
            <el-table-column label="盈亏" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol-scope.row.CostPrice*scope.row.PositionVol),0)}}</span>
              </template>                  
            </el-table-column>  
            <el-table-column label="当前市值" prop="" width="80" align="center">
               <template slot-scope="scope">
                <span>{{commonUtil.formatNumFix((scope.row.CurrentPrice*scope.row.PositionVol),0)}}</span>
              </template>                  
            </el-table-column>                                      
            <el-table-column label="交易市场" prop="TradeMkPlace" width="100" align="center">
            </el-table-column>                                                                                         
            <el-table-column label="" prop="">
               <template slot-scope="scope">
                <el-button type="info" @click="viewDialogUpd(2,scope.row)" size="mini" style="margin-left:10px">修改</el-button>   
              </template>
            </el-table-column>                                    
        </el-table>

      </div>

    <!--页码-->
    <div style='margin-top:20px;text-align: center;'>
        <el-pagination background
          layout='total,sizes, prev, pager, next, jumper'
          :total='total'
          :page-sizes="[20,50,100,200]"
          :page-size='pagination.perPageNum'
          :current-page='pagination.curPage'
          @current-change='handleCurrentChange'
          @size-change="handleSizeChange">
        </el-pagination>
    </div>    
    
    </div>

  </div>

</template>
<script>
import editor from '@/components/QuillEditor.vue';
export default {
  name: 'PositionAnalysis',
  data() {
    return {
      fn:'test',      
      posturl:'/PositionAnalysis',
      loading: false,     

      viewData:{
        gridData:[],
        count: 0
      },

      formData:{
        multipleSelection:[],
        CompanyCode:'',
        PositionStatusOptions: [{
                value: '',
                label: '持仓(全部)'
            }, {
                value: '1',
                label: '持仓中'                
            }, {
                value: '2',
                label: '已清仓'
            }],
        PositionStatus:"1"        
      },

      pagination: {
          perPageNum:20,//默认
          curPage: 1 //页码
      },

      dialogForm:{
        dialogVisible: false,
        Id:"",
        AnalysisContent:""
      },

      dialogFormUpd:{
        dialogVisible: false,
        type:'1', //1:添加 2：修改
        Id:"",
        PositionStatusOptions: [{
                value: 1,
                label: '持仓中'                
            }, {
                value: 2,
                label: '已清仓'
            }],   
        PositionStatus:1,
        PositionStartTime:'',
        PositionEndTime:'',
        CompanyCode:'',
        CompanyName:'',
        PositionVol:'',
        CostPrice:'',
        CurrentPrice:'',
        TradeMkPlaceOptions: [{
                value: '深圳A股',
                label: '深圳A股'
            }, {
                value: '上海A股',
                label: '上海A股'                
            }, {
                value: '基金',
                label: '基金'
            }],        
        TradeMkPlace:''
        
      }      
     
    }
  },
  /* 这个是vue的钩子函数，当new Vue()实例创建完毕后执行的函数 */
  created () {   

    var _this = this;

    _this.search();

  },
  mounted(){
  },  
  updated(){
  },
  components: { 
    editor
  },
  //计算属性
  computed: {
    //检索结果记录数
    total:function() {
        var _this = this;
        return Number(_this.viewData.count);
    }
  },
  //方法
  methods: {

    //检索
    search() {

      var _this = this;

      try{
        
          _this.loading = true;

        var data = {
            CurPage: _this.pagination.curPage,
            PerPageNum: _this.pagination.perPageNum,
            CompanyCode:_this.formData.CompanyCode,
            PositionStatus:_this.formData.PositionStatus
          }

        _this.$http.post(_this.posturl + "/search",data)
        .then(function (response) {

          if (response.data.Status == "false") {

            _this.$message.error(response.data.ErrMsg);

          } else {

            // console.log(response);

            var result = response.data.ResultData;

            _this.viewData.gridData = result.GridData;
            _this.viewData.count = result.Count;

          }   

          _this.loading = false;
          
        })
        .catch(function (error) {

          console.log(error);

        }); 

      }catch(err){

        //_this.$message.error(err.message);
        alert(err.message);

      } 

    },

    //换页
    handleCurrentChange:function(pageNo) {
        var _this = this;
        _this.pagination.curPage = pageNo;
        _this.search();
    },    

    //更改size
    handleSizeChange:function(val) {
        var _this = this;
        _this.pagination.perPageNum = val;
        _this.search();
    },  
    
    //选择列表
    handleSelectionChange:function(val) {
        var _this = this;
        _this.formData.multipleSelection = val;
    },    

    //显示对话框
    viewDialog(row){
      var _this = this;

      _this.dialogForm.Id = row.Id;
      _this.dialogForm.AnalysisContent = row.AnalysisContent;

      //console.log(row.AnalysisContent);

      _this.dialogForm.dialogVisible = true;

    },

    //添加/修改对话框
    viewDialogUpd(type,row){
      
      var _this = this;

      _this.dialogFormUpd.type = type;

      _this.dialogFormUpd.PositionStatus = 1;
      _this.dialogFormUpd.PositionStartTime = "";
      _this.dialogFormUpd.PositionEndTime = "";
      _this.dialogFormUpd.CompanyCode = "";
      _this.dialogFormUpd.CompanyName = "";
      _this.dialogFormUpd.PositionVol = "";
      _this.dialogFormUpd.CostPrice = "";
      _this.dialogFormUpd.CurrentPrice = "";
      _this.dialogFormUpd.TradeMkPlace = "";

      if(_this.dialogFormUpd.type=='2'){

        _this.dialogFormUpd.Id = row.Id;
        _this.dialogFormUpd.PositionStatus = row.PositionStatus;
        _this.dialogFormUpd.PositionStartTime = _this.commonUtil.formatData(row.PositionStartTime);
        _this.dialogFormUpd.PositionEndTime = _this.commonUtil.formatData(row.PositionEndTime);
        _this.dialogFormUpd.CompanyCode = row.CompanyCode;
        _this.dialogFormUpd.CompanyName = row.CompanyName;
        _this.dialogFormUpd.PositionVol = row.PositionVol;
        _this.dialogFormUpd.CostPrice = row.CostPrice;
        _this.dialogFormUpd.CurrentPrice = row.CurrentPrice;
        _this.dialogFormUpd.TradeMkPlace = row.TradeMkPlace;  

      }

      _this.dialogFormUpd.dialogVisible = true;

    },

    //关闭对话框
    handleClose(done) {
      
      this.$confirm('确认关闭？')
        .then(() => {
          done();
        })
        .catch(() => {});
    },

    onEditorChange(html) {

      var _this = this;
      
      _this.dialogForm.AnalysisContent = html;

      // console.log(_this.dialogForm.content);

    },

    //保存分析内容
    saveAnalysisContent(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var data = {
              Id: _this.dialogForm.Id,
              AnalysisContent: _this.dialogForm.AnalysisContent
            }

          _this.$http.post(_this.posturl + "/saveanlysiscontent",data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);

              _this.search();
              _this.dialogForm.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

    //表格索引
    indexMethod(index) {
        var _this = this;
        return (_this.pagination.curPage - 1) * _this.pagination.perPageNum + index + 1;
    },

    //添加/修改
    savePositionAnalysis(){

      var _this = this;

      try{
        
          //console.log(_this.dialogForm.AnalysisContent);

          _this.loading = true;

          var action = _this.dialogFormUpd.type=='1'?'/add':'/update';

          var data = _this.dialogFormUpd;

          _this.$http.post(_this.posturl + action,data)
          .then(function (response) {

            if (response.data.Status == "false") {

              _this.$message.error(response.data.ErrMsg);

            } else {

              // console.log(response);

              _this.search();
              _this.dialogFormUpd.dialogVisible = false;

            }   

            _this.loading = false;
            
          })
          .catch(function (error) {

            console.log(error);

          }); 

        }catch(err){

          //_this.$message.error(err.message);
          alert(err.message);

        } 

    },

  }
}
</script>

<style scoped>

  .boxmain {
    width:1100px;
    margin: 0px auto;
    text-align: left;
    font-size: 15px;
  }

</style>
